// eslint-disable-next-line
const Validate_Server = "https://api.${region}.500apps.com/500conference/validate";
import axios from "axios";
import Data from "../core/data";

class ValidateOTP extends Data {
  constructor() {
    super(Validate_Server);
    this.disablePCors();
  }
  async verify(form) {
    let results;
    // eslint-disable-next-line no-unused-vars
    let region = form.env;
    this.url = eval("`" + Validate_Server + "`");
    delete form.env;
    try {
      results = await axios.post(this.getPCors(), form);
    } catch (err) {
      results = false;
    }
    return results;
  }
}

export default new ValidateOTP();
