<template>
  <b-modal id="validate-user" hide-footer no-close-on-backdrop body-class="p-3 bg-soft-light rounded" dialog-class="modal-425px" title="Protected room">
    <b-form @submit.prevent="verify" ref="form" class="card mb-0 card-body">
      <b-row class="m-0">
        <b-col md="12" class="px-2">
          <b-form-group label="Name" label-class="pb-0 mb-0" class="mb-3 pb-2">
            <b-input-group class="input-group-merge">
              <b-form-input v-model="form.name" type="text" placeholder="Enter your name" required class="rounded-0 border-left-0 rounded-right" />
              <b-input-group-prepend>
                <b-input-group-text class="pr-0">
                  <i class="fe fe-user" />
                </b-input-group-text>
              </b-input-group-prepend>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="12" class="px-2">
          <b-form-group label="Email" label-class="pb-0 mb-0" class="mb-3 pb-2">
            <b-input-group class="input-group-merge">
              <b-form-input v-model="form.email" type="email" placeholder="Enter your email address" required class="rounded-0 border-left-0 rounded-right" />
              <b-input-group-prepend>
                <b-input-group-text class="pr-0">
                  <i class="fe fe-mail" />
                </b-input-group-text>
              </b-input-group-prepend>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="12" class="px-2">
          <b-form-group label="Verification Code" class="mb-1" label-class="pb-0 mb-0">
            <b-input-group class="input-group-merge">
              <b-form-input v-model="form.code" type="number" placeholder="Enter verification code" required class="rounded-0 border-left-0 rounded-right" />
              <b-input-group-prepend>
                <b-input-group-text class="pr-0">
                  <i class="fe fe-hash" />
                </b-input-group-text>
              </b-input-group-prepend>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="12" class="px-2">
          <div class="border-top d-flex justify-content-end  align-items-center mt-4 pt-4">
            <b-badge variant="soft-danger" class="px-3 py-2 mr-4 font-weight-500 rounded-sm" v-if="errorMessage">{{ errorMessage }}</b-badge>
            <b-button type="submit" variant="primary" class="px-4">Verify</b-button>
          </div>
        </b-col>
      </b-row>
    </b-form>
  </b-modal>
</template>
<script>
  import ValidateOTP from "@/js/conference/validate.js";
  export default {
    props: {
      roomInfo: {
        type: Object,
        default: () => {
          return {};
        },
      },
    },
    data() {
      return {
        form: {},
        errorMessage: "",
      };
    },
    methods: {
      // Verifies the email and otp if true then it emits.
      async verify() {
        let response = await ValidateOTP.verify(
          {
            domain_id: this.roomInfo.domain_id,
            email: this.form.email,
            otp: this.form.code,
            visibility: (this.roomInfo.visibility + "").toLowerCase(),
            room_name: this.roomInfo.name,
            link: window.location.href,
            env: this.roomInfo.env,
          },
          true,
        );
        if (response.data.verification == true) {
          response.data.name = this.form.name;
          response.data.email = this.form.email;
          this.$emit("verified", response.data);
        } else {
          this.errorMessage = "Please enter valid details";
          setTimeout(() => {
            this.errorMessage = "";
          }, 1500);
        }
      },
    },
  };
</script>
<style>
  .rounded-right {
    border-top-right-radius: 0.325rem !important;
    border-bottom-right-radius: 0.325rem !important;
  }
  .right-8px {
    right: 8px !important;
  }
  .top-8px {
    top: 8px !important;
  }
